import ILogSource from './ILogSource';
import Validate from '../../common/Validate';
import ServiceKey from '../../serviceScope/ServiceKey';
import ServiceScope from '../../serviceScope/serviceScope';

/**
 * @internal
 */
export default class LogSource implements ILogSource {
  public id: string;

  /**
   * Factory method to create an instance of the LogSource class
   * @returns a reference to an instance of the LogSource class
   */
  public static create(id: string): LogSource {
    return new LogSource(id);
  }

  /**
   * @private
   * PRIVATE CONSTRUCTOR - DO NOT CALL THIS FROM YOUR OWN CODE.
   */
  constructor(id: string) {
    Validate.isNotNullOrUndefined(id, 'id');

    this.id = id;
  }

  /**
   * @return {boolean} - if the LogSource is empty.
   */
  public isEmpty(): boolean {
    return this.id.length === 0;
  }
}

export const logSourceServiceKey: ServiceKey<LogSource>
  = ServiceKey.createCustom<LogSource>('sp-client-base:LogSource', (serviceScope: ServiceScope): LogSource => {
    return new LogSource('');
  });